body {
  background-color: #181D26;
}

.memory {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 300px;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.memory > .card {
  background-color: white;
    font-size: 2em;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    -webkit-flex-basis: calc(100% / 6 - 0.4em);
            flex-basis: calc(100% / 6 - 0.4em);
    outline: 0.08em solid #ff004e;
    margin: 0.2em;
    display: -webkit-flex;
    display: flex;
    cursor: default;
  }

  .memory > .card.justMatched,
  .memory > .card.justMismatched {
    outline: 0.1em solid green;
  }
  .memory > .card.justMismatched {
    outline-color: red;
  }
  
  .memory > .card.visible {
    cursor: not-allowed;
  }
  
  .memory > .card > .symbol {
    margin: auto;
  }

.memory > .guesses {
    font-size: 1em;
    width: calc(100% - 0.4em);
    margin: 0.2em;
    text-align: center;
    font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
    border: solid 2px;
    border-radius: 8px;
    background-color: white;
  }

.hallOfFame {
    width: 100%;
    border-collapse: collapse;
  }
  
  .hallOfFame .date,
  .hallOfFame .guesses {
    width: 20%;
    text-align: center;
  }
  
  .hallOfFame tr:nth-child(even) {
    background: #eee;
  }
.highScoreInput {
    font-size: 1em;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    width: calc(100% - 0.4em);
    margin: 0.2em;
  }
  .highScoreInput input {

    width: 100%;
    margin-bottom: 0.2em;
    font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
  }

  .brand {
    width: 200px;
    height: 200px;
    position: relative;
    margin: 0 auto;
    
  }
  .brand:before {
    
    background-size: 100% 100%;
    content: "MEMORY";
    line-height: 205px;
    color: #ff004e;
    font-size: 20px;
    font-family: "HEAVITAS", "Helvetica Rounded", Arial, sans-serif;
    font-weight: bold;
    text-align: center;
    width: 200px;
    height: 200px;
    margin: 0 auto;
    position: absolute;
    
    z-index: 99999 !important;
  }
  
  .logo{
    -webkit-animation: 10s linear infinite;
            animation: 10s linear infinite;
    -webkit-animation-name: rotate-0;
            animation-name: rotate-0;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    
  }
  
  
  svg path{
    positon:absolute;
    fill:none;
    stroke-width:2;
    top:0;
    left:0;
    /*mix-blend-mode: multiply;*/
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(0deg) ;
            transform: rotate(0deg) ;
    -webkit-animation: 4s linear infinite;
            animation: 4s linear infinite;
  }
  
  .logo svg path:nth-child(1) {
    opacity: 1;
    -webkit-animation-name: rotate-1;
            animation-name: rotate-1;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
  }
  .logo svg path:nth-child(2) {
    opacity: .5;
    -webkit-animation-name: rotate-2;
            animation-name: rotate-2;
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
  }
  .logo svg path:nth-child(3) {
    opacity: .8;
    -webkit-animation-name: rotate-3;
            animation-name: rotate-3;
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
  }
  
  @-webkit-keyframes scale-1 {
    0%{
      -webkit-transform: scale(1);
              transform: scale(1);
    }50% {
      -webkit-transform: scale(.9);
              transform: scale(.9);
    }100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    } 
  }
  
  @keyframes scale-1 {
    0%{
      -webkit-transform: scale(1);
              transform: scale(1);
    }50% {
      -webkit-transform: scale(.9);
              transform: scale(.9);
    }100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    } 
  }
  
  @-webkit-keyframes rotate-0 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes rotate-0 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  } 
  
  @-webkit-keyframes rotate-1 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  } 
  
  @keyframes rotate-1 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @-webkit-keyframes rotate-2 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
  }
  
  @keyframes rotate-2 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
  }
  
  @-webkit-keyframes rotate-3 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  }
  
  @keyframes rotate-3 {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
      to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  }
